.header {
    display: flex;
    align-items: center;
    background-color: #131921;
    height: 60px;
    padding: 0 20px;
    position: sticky;
    top: 0;
    z-index: 100;
  }
  
  .header__logo {
    width: 100px;
    margin-right: 20px;
  }
  
  .header__location {
    color: white;
    margin-right: 20px;
  }
  
  .header__locationLineOne {
    font-size: 10px;
  }
  
  .header__locationLineTwo {
    font-size: 13px;
    font-weight: 800;
  }
  
  .header__search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
    background-color: white;
    padding-left: 10px;
    margin-right: 20px;
  }
  
  .header__searchDropdown {
    border: none;
    background-color: #f3f3f3;
    padding: 5px;
    border-radius: 4px;
    margin-right: 5px;
  }
  
  .header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
  }
  
  .header__searchIcon {
    padding: 5px;
    height: 22px !important;
    background-color: #febd69;
    border-radius: 0 24px 24px 0;
  }
  
  .header__nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  
  .header__link {
    text-decoration: none;
    color: white;
    margin-left: 10px;
  }
  
  .header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
  }
  
  .header__optionLineOne {
    font-size: 10px;
  }
  
  .header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
  }
  
  .header__optionBasket {
    display: flex;
    align-items: center;
    color: white;
  }
  
  .header__basketCount {
    margin-left: 10px;
    margin-right: 10px;
  }
  