.login__logo{
    margin-top:20px;
    margin-bottom: 20px;
    width: 100px;
     object-fit:contain;  /* To maintain aspect ratio */
}

.login{
    background-color: white;
    display:flex;
    height: 100vh ;
    flex-direction:column;
    align-items:center;
}
.login__container{
    width: 300px;
    display: flex;
    flex-direction:column;
    padding:20px;
    border: 1px solid lightgrey;
}
.login__container > h1{
    font-weight:500;
    margin-bottom: 20px;
}
.login__container > form > h5{
    margin-bottom: 5px;
}

.login__container > form > input{
    height: 30px;
    margin-bottom: 10px;
    background-color:white;
    width: 98%;
}
.login__container > p {
    margin-top: 15px;
    font-size: 12px;

}
.login__signInButton{
    background: #f0c14b;
    border: 1px solid ;
    border-radius:2px;
    width: 100%;
    height: 30px;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}

.login__registerButton{
    border: 1px solid ;
    border-radius:2px;
    width: 100%;
    height: 30px;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}